import { EditorApi } from "./types";

const clickHandler = ({ event, editor }: { event: MouseEvent } & EditorApi) => {
  const anchor: HTMLAnchorElement | null = (event.target as HTMLElement).closest("A");
  if (anchor) {
    event.preventDefault();
    editor.navigateTo({ href: anchor.href });
  }
};

const runNavigationHandler = ({ editor }: EditorApi): void => {
  document.addEventListener("click", (event) => clickHandler({ event, editor }), { capture: true });
};

export { runNavigationHandler };
